.music {
  top: 20px;
  padding: 20px;
  display: flex;
  justify-content: flex-end; /* Align buttons to the right end of the container */
  gap: 10px; /* Add some space between the buttons */
  transition: color 0.5s ease-in-out;
  }

#music-toggle,
#next-music,
#previous-music {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  transition: color 0.5s ease-in-out;
  height: 40px;
  cursor: pointer;
  position: fixed;
  margin: 20px;
  border-radius: 0.5em;
}

#music-toggle:hover,
#next-music:hover,
#previous-music:hover {
  transform: scale(1.2);
  background-color: rgb(21, 87, 3);
  transition: color 0.5s ease-in-out;
}





.modal-container {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
  }
  

  .modal-content {
    background-color: #fff;
    margin: 10% auto;
    padding: 20px;
    border: 1px solid #888;
    max-width: 600px;
  }
  
 
  .close-button {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .close-button:hover {
    color: black;
  }
  #about-button{
    height: 45px;
    cursor: pointer;
    background-color: #eee;
    border-radius: 2em;
    transition: color 0.5s ease-in-out;
  }
  #about-button:hover{
    color: rgb(21, 87, 3);
    background-color: rgb(21, 87, 3);
    transition: color 0.5s ease-in-out;
  }

